import { ThemeProvider } from 'styled-components';
import PropTypes from 'prop-types';

import {
  colors,
  breakpoints,
  fonts,
  mediaQueries,
  space,
  widths,
  componentStyles,
  GlobalStyle,
} from './utils';

export const themeProps = {
  colors,
  fonts,
  breakpoints,
  mediaQueries,
  space,
  widths,
  componentStyles,
};

export const Theme = ({ children }) => (
  <ThemeProvider theme={themeProps}>
    <GlobalStyle />
    {children}
  </ThemeProvider>
);

Theme.propTypes = {
  children: PropTypes.node.isRequired,
};

export const Space = Object.keys(themeProps.space);
