import styled from 'styled-components';
import PropTypes from 'prop-types';
import { Box } from '../Box';
import { Heading } from '../Typography';

const Root = styled(Box)`
  background-color: #fff;
  box-shadow: 0px 20px 40px rgba(0, 0, 0, 0.08);
  padding-bottom: 58px;
  max-height: 300px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  border-radius: 16px;
  ${(props) => props.theme.mediaQueries.lg} {
    min-height: 340px;
  }
`;

const CardImageContainer = styled.div`
  position: relative;
  min-height: 200px;
  width: 100%;
  margin-bottom: 10px;
  ${(props) => props.theme.mediaQueries.lg} {
    min-height: 176px;
  }

  svg {
    position: absolute;
    max-width: 100%;
    max-height: 110%;
    bottom: 0;
    right: ${(props) => (props.alignRight ? '0' : 'initial')};
    transform: ${(props) => (!props.alignRight ? 'translateX(-50%)' : 'initial')};
    left: ${(props) => (!props.alignRight ? '50%' : 'initial')};

    ${(props) => props.theme.mediaQueries.lg} {
      max-width: initial;
      max-height: initial;
      ${(props) => props.customStyle}
    }
  }
`;

const SubTitle = styled.div`
  color: ${(props) => props.color};
  text-align: center;
  font-weight: 600;
`;

const Card = ({ mb, customImageStyle, image, title, subtitle, subtitleColor, alignImageRight }) => (
  <Root mb={mb}>
    <CardImageContainer customStyle={customImageStyle} alignRight={alignImageRight}>
      {image}
    </CardImageContainer>
    <Heading variant="500" color="black" textAlign="center">
      {title}
    </Heading>
    {subtitle !== null && <SubTitle color={subtitleColor}>{subtitle}</SubTitle>}
  </Root>
);

Card.propTypes = {
  image: PropTypes.node.isRequired,
  title: PropTypes.string.isRequired,
  alignImageRight: PropTypes.bool,
  mb: PropTypes.string,
  customImageStyle: PropTypes.string,
  subtitle: PropTypes.string,
  subtitleColor: PropTypes.string,
};

Card.defaultProps = {
  alignImageRight: false,
  customImageStyle: null,
  mb: null,
  subtitle: null,
  subtitleColor: '#333',
};

export default Card;
