/* eslint-disable react/prop-types */

import React from 'react';
import styled from 'styled-components';

import { Input, Select, Button } from 'components';

const xlsx = require('xlsx');

const ControlsRoot = styled.div`
  width: 100%;

  display: flex;
  gap: 20px;
  align-content: center;
  align-items: baseline;

  margin-bottom: 20px;
`;

const Controls = ({ data, filterData, onChange }) => {
  const downloadExcel = () => {
    const json = JSON.parse(JSON.stringify(data));
    const workBook = xlsx.utils.book_new();
    const workSheet = xlsx.utils.json_to_sheet(json);
    xlsx.utils.book_append_sheet(workBook, workSheet);
    xlsx.writeFile(
      workBook,
      `Data Assignment PABCL ${filterData.angkatan} ${filterData.materi}.xlsx`
    );
  };

  return (
    <ControlsRoot>
      <Select name="angkatan" onChange={onChange} value={filterData.angkatan}>
        <option value="">Semua Angkatan</option>
        <option value="2023">Angkatan 2023</option>
        <option value="2022">Angkatan 2022</option>
      </Select>
      <Select name="materi" onChange={onChange} value={filterData.materi}>
        <option value="">Semua Materi</option>
        <option value="uiux">UI/UX</option>
        <option value="mobile">Mobile</option>
        <option value="iot">IoT</option>
        <option value="web">Web</option>
        <option value="data">Data</option>
        <option value="game">Game</option>
        <option value="other">other</option>
      </Select>
      <Input
        name="keyword"
        onChange={onChange}
        value={filterData.keyword}
        placeholder="Search by NIM or Name"
      />
      <Button onClick={downloadExcel}>Download Excel</Button>
    </ControlsRoot>
  );
};

export default Controls;
