import styled from 'styled-components';
import { PatternBackground } from 'assets';
import PropTypes from 'prop-types';
import Content from './Content';
import Column from './Column';

const Root = styled.section`
  min-height: ${(props) => (props.whiteContainer ? '100vh' : '449px')};

  color: #fff;
  background: linear-gradient(209.76deg, #27dda1 0%, #3293d0 51.75%, #3e91d7 63.87%, #026eda 100%);
  padding-top: 190px;
  position: relative;
  overflow: hidden;

  @media screen and (max-height: 667px) {
    /* For Device with non-Full HD Display */
    min-height: ${(props) => (props.whiteContainer ? '800px' : '449px')};
    padding-top: 150px;
  }

  ${(props) => props.theme.mediaQueries.md} {
    min-height: ${(props) => (props.whiteContainer ? '781px' : '480px')};
    max-height: 781px;
  }
`;

const BgHero = styled.div`
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  z-index: 0;
  display: flex;
  justify-content: center;
`;

const HeroContent = styled(Content)`
  z-index: 4;
  position: relative;
  text-align: center;
  margin-bottom: 80px;
  ${(props) => props.theme.mediaQueries.sm} {
    text-align: left;
  }
`;

const HeroWhiteContainer = styled.div`
  position: absolute;
  width: 100vw;
  bottom: 0px;
  height: 781px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  justify-content: center;
  user-select: none;
  background-image: url('/assets/hero_white_container.svg');
  background-position-x: center;
  background-position-y: 85%;
  background-repeat: no-repeat;
  background-size: cover;
  z-index: 2;

  &:after {
    content: '';
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    height: 5px;
    background-color: #fff;
  }
`;

const HeroBase = ({ children, withWhiteContainer }) => (
  <Root whiteContainer={withWhiteContainer}>
    <BgHero>
      <PatternBackground />
    </BgHero>
    <HeroContent>
      <Column>{children}</Column>
    </HeroContent>
    {withWhiteContainer && <HeroWhiteContainer />}
  </Root>
);

HeroBase.propTypes = {
  children: PropTypes.node,
  withWhiteContainer: PropTypes.bool,
};

HeroBase.defaultProps = {
  children: null,
  withWhiteContainer: false,
};

export default HeroBase;
