/* eslint-disable react/prop-types */

import styled from 'styled-components';
import { Table, Pagination } from 'bima-design';
import { useTable, usePagination } from 'react-table';
import React, { useRef, forwardRef, useMemo, useState, useEffect } from 'react';

const StyledTable = styled(Table)`
  width: 100%;
`;

const PaginationContainer = styled('div')`
  width: 100%;
  display: flex;
  justify-content: center;

  padding: 40px 0 0 0;
`;

const index = forwardRef(({ data, columns, tableName }, ref) => {
  const tableRef = useRef(ref);

  const [pageNumber, setPageNumber] = useState(0);
  const [tableID] = useState(`table-${tableName}`);

  const memoizedData = useMemo(() => data, [data]);
  const memoizedColumns = useMemo(() => columns, []);

  const {
    page,
    gotoPage,
    pageCount,
    prepareRow,
    headerGroups,
    getTableProps,
    getTableBodyProps,
  } = useTable(
    {
      data: memoizedData,
      columns: memoizedColumns,
      initialState: { pageIndex: 0, pageSize: 10 },
    },
    usePagination
  );

  const paginationChangeHandler = (number) => {
    tableRef.current.scrollIntoView({
      block: 'start',
      behavior: 'smooth',
    });

    setPageNumber(number - 1);
  };

  useEffect(() => {
    gotoPage(pageNumber);
  }, [pageNumber]);

  useEffect(() => {
    setPageNumber(0);
  }, [data]);

  return (
    <div ref={tableRef}>
      <StyledTable id={tableID} {...getTableProps()}>
        <thead>
          {headerGroups.map((headerGroup) => {
            return (
              <tr {...headerGroup.getHeaderGroupProps()}>
                {headerGroup.headers.map((headerColumns) => {
                  return (
                    <th {...headerColumns.getHeaderProps()}>{headerColumns.render('title')}</th>
                  );
                })}
              </tr>
            );
          })}
        </thead>
        <tbody {...getTableBodyProps()}>
          {page.map((row) => {
            prepareRow(row);

            return (
              <tr {...row.getRowProps()}>
                {row.cells.map((cell) => {
                  return <td {...cell.getCellProps()}>{cell.render('Cell')}</td>;
                })}
              </tr>
            );
          })}
        </tbody>
      </StyledTable>
      <PaginationContainer>
        <Pagination
          nextLabel="Next"
          prevLabel="Previous"
          totalPage={pageCount}
          currentPage={pageNumber + 1}
          onPageChangeCallback={paginationChangeHandler}
        />
      </PaginationContainer>
    </div>
  );
});

export default index;
