/* eslint-disable no-shadow */
/* eslint-disable import/no-cycle */
/* eslint-disable react/prop-types */

import { get } from 'js-cookie';
import React, { useEffect } from 'react';

import { zustandState } from 'services';

const Authentication = ({ children }) => {
  const state = {
    setUser: zustandState((state) => state.setUser),
    setIsAuthenticating: zustandState((state) => state.setIsAuthenticating),
  };

  const getAuthData = async (token) => {
    const { baseURL } = process.env;
    const { setUser, setIsAuthenticating } = state;

    const response = await fetch(`${baseURL}/api/me`, {
      method: 'GET',
      headers: {
        Accept: 'application/json',
        Authorization: `Bearer ${token}`,
      },
    });

    const responseToJSON = await response.json();
    const { data } = responseToJSON;

    const user = {
      ...data,
      token,
    };

    setUser(user);
    setIsAuthenticating(false);
  };

  useEffect(() => {
    const { tokenName } = process.env;

    const token = get(tokenName);

    if (token) {
      getAuthData(token);
    }
  }, []);

  return <div>{children}</div>;
};

export default Authentication;
