import styled from 'styled-components';
import PropTypes from 'prop-types';
import { Box } from '../Box';
import { Heading, Paragraph } from '../Typography';

const CardWithImage = styled(Box)`
  background-image: url(${(props) => props.image});
  background-color: #ccc;
  background-size: cover;
  background-position: center;
  min-height: 368px;
  border-radius: 16px;
  display: flex;
`;

const BgTextShadow = styled(Box)`
  margin-top: 212px;
  width: 100%;
  color: #fff;
  padding: 28px 44px;
  border-radius: 0 0 16px 16px;
  background: linear-gradient(
    180deg,
    rgba(0, 0, 0, 0) 0%,
    rgba(0, 0, 0, 0.6) 45.83%,
    rgba(0, 0, 0, 0.6) 100%
  );
`;

const CardBgImage = ({ bgImage, title, description }) => (
  <CardWithImage image={bgImage}>
    {/* The purpose of the img below is to improve rendering times */}
    <img src={bgImage} alt="" style={{ display: 'none' }} />
    <BgTextShadow>
      <Heading variant="600" mb={28}>
        {title}
      </Heading>
      <Paragraph variant="300">{description}</Paragraph>
    </BgTextShadow>
  </CardWithImage>
);

CardBgImage.propTypes = {
  bgImage: PropTypes.string.isRequired,
  title: PropTypes.string.isRequired,
  description: PropTypes.string.isRequired,
};

export default CardBgImage;
