/* eslint-disable react/jsx-filename-extension */

import React from 'react';
import Router from 'next/router';

import { zustandState } from 'services';

const withoutUserAuth = (Components) => {
  return (props) => {
    const state = {
      user: zustandState((state) => state.user),
    };

    if (typeof window !== 'undefined') {
      const { user } = state;

      if (user) {
        Router.replace('/admin');
      }
    }

    return <Components {...props} />;
  };
};

export default withoutUserAuth;
