import { useRef } from 'react';
import styled, { keyframes, css } from 'styled-components';
import PropTypes from 'prop-types';
import LazyLoad from 'react-lazyload';
import { Box } from '../Box';
import { Paragraph } from '../Typography';

const Root = styled(Box)`
  display: flex;
  flex-direction: column;
  align-items: center;
`;

const BaseImageSize = css`
  width: ${(props) => (props.small ? '150px' : '190px')};
  height: ${(props) => (props.small ? '150px' : '263px')};
`;

const BaseImagePosition = css`
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
`;

const placeHolderShimmer = keyframes`
  0%{
      background-position: -468px 0
  }
  100%{
      background-position: 468px 0
  }
`;

const ImageContainer = styled.div`
  ${BaseImageSize}
  position: relative;
`;

const Placeholder = styled.div`
  ${BaseImagePosition}
  animation-duration: 1.8s;
  animation-fill-mode: forwards;
  animation-iteration-count: infinite;
  animation-name: ${placeHolderShimmer};
  animation-timing-function: linear;
  background-color: #f1f1f1;
  background: linear-gradient(to right, #f3f3f3 5%, #eee 20%, #f3f3f3 35%);
  background-size: 500px 640px;
  border-radius: 10px;
`;

const Image = styled.img`
  ${BaseImagePosition}
  width: 100%;
  height: 100%;
  border-radius: 10px;
  object-fit: cover;
  opacity: 0;
  transition: opacity 0.3s ease-out;
  &.isLoaded {
    opacity: 1;
  }
`;

const Name = Paragraph.withComponent('div');
const Title = Paragraph.withComponent('div');

const CardPerson = ({ picSrc, name, position, smallPic }) => {
  const refImage = useRef();
  const refPlaceholder = useRef();
  const showImage = () => {
    refImage.current.classList.add('isLoaded');
    refPlaceholder.current.remove();
  };
  return (
    <Root>
      <ImageContainer small={smallPic}>
        <Placeholder ref={refPlaceholder} />
        <LazyLoad>
          <Image
            ref={refImage}
            src={picSrc}
            small={smallPic}
            alt={name}
            onLoad={showImage}
            onError={showImage}
          />
        </LazyLoad>
      </ImageContainer>
      <Name variant="400" mt={32} color="black" textAlign="center">
        {name}
      </Name>
      <Title variant="300" textAlign="center">
        {position}
      </Title>
    </Root>
  );
};

CardPerson.propTypes = {
  picSrc: PropTypes.string.isRequired,
  name: PropTypes.string.isRequired,
  position: PropTypes.string,
  smallPic: PropTypes.bool,
};

CardPerson.defaultProps = {
  smallPic: false,
  position: null,
};

export default CardPerson;
