import { motion } from 'framer-motion';
import styled, { css } from 'styled-components';
import PropTypes from 'prop-types';
import { Heading, Paragraph } from '../Typography';
import HeroBase from './HeroBase';

const sharedStyle = css`
  /* opacity: 0; */
  transition: opacity 0.8s cubic-bezier(0.075, 0.82, 0.165, 1) 0.2s;
  &.loaded {
    opacity: 1;
  }
`;

const HeroTitle = styled(Heading)`
  ${sharedStyle}
`;

const HeroSubtitle = styled(Paragraph)`
  ${sharedStyle}
  margin-bottom: ${(props) => props.theme.space.lg}px;
  ${(props) => props.theme.mediaQueries.sm} {
    margin-bottom: 0;
  }
`;

const HeroFlat = ({ title, description }) => (
  <HeroBase>
    <motion.div
      initial={{ x: -3, opacity: 0 }}
      animate={{ x: 0, opacity: 1 }}
      exit={{ x: 6, opacity: 0 }}
      transition={{ ease: 'easeInOut', duration: 0.2, delay: 0.1 }}
    >
      <HeroTitle variant="900" maxWidth={700}>
        {title}
      </HeroTitle>
      <HeroSubtitle mt="sm" color="grey2" variant="500" maxWidth={900}>
        {description}
      </HeroSubtitle>
    </motion.div>
  </HeroBase>
);

HeroFlat.propTypes = {
  title: PropTypes.string.isRequired,
  description: PropTypes.string.isRequired,
};

export default HeroFlat;
