import styled, { css } from 'styled-components';

const Column = styled('div')`
  margin: 0 auto;
  width: 100%;
  max-width: ${(props) => props.theme.widths.md}px;

  ${(props) =>
    props.size === 'lg' || props.size === 'xl'
      ? css`
          ${props.theme.mediaQueries.lg} {
            max-width: ${props.theme.widths.lg}px;
          }
        `
      : ''}

  ${(props) =>
    props.size === 'xl'
      ? css`
          ${props.theme.mediaQueries.xl} {
            max-width: ${props.theme.widths.xl}px;
          }
        `
      : ''}
`;

Column.defaultProps = {
  size: 'xl',
};

export default Column;
