/* eslint-disable no-shadow */

import Link from 'next/link';
import { v4 as id } from 'uuid';
import PropTypes from 'prop-types';
import { motion } from 'framer-motion';
import styled from 'styled-components';
import { useRouter } from 'next/router';
import React, { useState, useEffect } from 'react';

import { zustandState } from 'services';

const Root = styled(motion.nav)`
  visibility: ${(props) => (props.open ? 'visible' : 'hidden')};
  opacity: ${(props) => (props.open ? 1 : 0)};
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  overflow-y: scroll;
  background-color: white;
  padding: 150px 24px;
  transition: all 0.25s ease-out;

  ${(props) => props.theme.mediaQueries.lg} {
    transition: none;
    visibility: visible;
    opacity: 1;
    width: initial;
    height: initial;
    overflow-y: hidden;
    padding: 0;
    background-color: transparent;
    position: initial;
    display: block;
  }
`;

const StyledLink = styled(motion.a)`
  text-transform: uppercase;
  display: block;
  font-size: 32px;
  text-align: center;
  padding-top: 10px;
  padding-bottom: 10px;
  color: ${(props) => (props.isActive ? props.theme.colors.green : 'initial')};

  &:hover {
    opacity: ${(props) => (props.isActive ? 1 : 0.7)};
  }
  &:active,
  &:focus {
    opacity: ${(props) => (props.isActive ? 1 : 0.5)};
  }

  ${(props) => props.theme.mediaQueries.lg} {
    padding: 0;
    margin: 23px;
    font-size: initial;
    display: inline-block;
    color: ${(props) => (props.isActive ? '#fff' : '#e1e1e1')};

    &:last-child {
      padding-right: 0;
    }

    &:hover {
      color: #fff;
      opacity: 1;
    }
    &:focus {
      opacity: 1;
    }
  }
`;

const variants = {
  open: {
    transition: { staggerChildren: 0.05, delayChildren: 0.2 },
  },
};

const linkVariants = {
  open: {
    y: 0,
    opacity: 1,
    transition: {
      y: { stiffness: 1000, velocity: -100 },
    },
  },
  closed: {
    y: 50,
    opacity: 0,
    transition: {
      y: { stiffness: 1000 },
    },
  },
};

const list = ['home', 'about', 'products', 'teams', 'blog', 'alumni'];

const NavList = ({ isOpen }) => {
  const router = useRouter();

  const [width, setWidth] = useState();

  const state = {
    user: zustandState((state) => state.user),
  };

  useEffect(() => {
    const handleResize = () => setWidth(window.innerWidth);
    handleResize();
    window.addEventListener('resize', handleResize);
    return () => {
      window.removeEventListener('resize', handleResize);
    };
  });

  const shouldHaveAnimation = width < 992;
  const openOrClosed = isOpen ? 'open' : 'closed';

  return (
    <Root
      open={isOpen}
      initial={false}
      animate={shouldHaveAnimation ? openOrClosed : null}
      variants={shouldHaveAnimation ? variants : null}
    >
      {list.map((item) => {
        const url = item === 'home' ? '/' : `/${item}`;

        return (
          <Link href={url} passHref key={id()}>
            <StyledLink
              variants={linkVariants}
              initial={shouldHaveAnimation ? { opacity: 0, y: 10 } : {}}
              isActive={router.pathname === url}
            >
              {item}
            </StyledLink>
          </Link>
        );
      })}

      {state.user ? (
        <Link href="/admin" passHref key={id()}>
          <StyledLink
            variants={linkVariants}
            initial={shouldHaveAnimation ? { opacity: 0, y: 10 } : {}}
            isActive={router.pathname === '/admin'}
          >
            Admin
          </StyledLink>
        </Link>
      ) : (
        <Link href="/login" passHref key={id()}>
          <StyledLink
            variants={linkVariants}
            initial={shouldHaveAnimation ? { opacity: 0, y: 10 } : {}}
            isActive={router.pathname === '/login'}
          >
            Login
          </StyledLink>
        </Link>
      )}
    </Root>
  );
};

NavList.propTypes = {
  isOpen: PropTypes.bool,
};

NavList.defaultProps = {
  isOpen: false,
};

export default NavList;
