import React from 'react';
import styled from 'styled-components';

const StyledInput = styled('input')`
  width: 100%;
  padding: 18px;
  margin-top: 12px;
  border-radius: 25px;
  border: 1px solid ${(props) => (props.error ? '#FF6565' : '#c1c1c1')};
  box-sizing: border-box;

  &:focus {
    border: 1px solid #2080d9;
    outline: none;
  }
`;

const Input = ({ ...props }) => {
  return <StyledInput {...props} />;
};

export default Input;
