import { useEffect, useState } from 'react';
import styled from 'styled-components';
import Link from 'next/link';
import { Box } from '../../Box';
import Logo from './Logo';
import NavList from './NavList';

const Root = Box.withComponent('header');

const NavWrapper = styled(Root)`
  position: absolute;
  top: 49px;
  left: 0;
  right: 0;
  width: 100%;
  z-index: 9;
  max-width: ${(props) => props.theme.widths.md}px;
  margin: auto;
  padding: 0 24px;
  display: flex;
  align-items: center;
  justify-content: space-between;

  ${(props) => props.theme.mediaQueries.md} {
    padding: 0;
  }

  ${(props) => props.theme.mediaQueries.lg} {
    max-width: ${(props) => props.theme.widths.lg}px;
  }

  ${(props) => props.theme.mediaQueries.xl} {
    max-width: ${(props) => props.theme.widths.xl}px;
  }
`;

const LogoWrapper = styled.h1`
  margin: 0;
  display: flex;
  justify-content: center;
  cursor: pointer;
`;

const MobileMenuToggle = styled.div`
  width: 33px;
  height: 33px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  z-index: 2;
  cursor: pointer;

  ${(props) => props.theme.mediaQueries.lg} {
    display: none;
  }

  span {
    width: 33px;
    height: 3px;
    border-radius: 3px;
    background-color: ${(props) => (props.active ? props.theme.colors.black : 'white')};
    display: block;
    margin-left: auto;
    transition: all 0.3s ease-out;
  }

  span:nth-child(1) {
    transform: ${(props) => (props.active ? 'rotate(-45deg) translate(-5px, 5px)' : 'none')};
  }
  span:nth-child(2) {
    width: 22px;
    margin-top: 5px;
    background-color: ${(props) => (props.active ? 'transparent' : 'white')};
  }
  span:nth-child(3) {
    margin-top: 5px;
    transform: ${(props) => (props.active ? 'rotate(45deg) translate(-6px, -6px)' : 'none')};
  }
`;

const Navigation = () => {
  const [navIsOpen, setNavIsOpen] = useState(false);

  useEffect(() => {
    document.body.classList.toggle('no-scroll', navIsOpen);
  }, [navIsOpen]);

  return (
    <NavWrapper>
      <Link href="/">
        <a aria-label="Home">
          <LogoWrapper>
            <Logo />
          </LogoWrapper>
        </a>
      </Link>
      <NavList isOpen={navIsOpen} />
      <MobileMenuToggle active={navIsOpen} onClick={() => setNavIsOpen(!navIsOpen)}>
        <span />
        <span />
        <span />
      </MobileMenuToggle>
    </NavWrapper>
  );
};

export default Navigation;
