/* eslint-disable no-shadow */
/* eslint-disable react/prop-types */
/* eslint-disable no-use-before-define */

import moment from 'moment';
import styled from 'styled-components';
import React, { useState, useEffect } from 'react';

import { ReactTable } from 'components';
import { zustandState } from 'services';

import Modal from './Modal';
import Controls from './Controls';

const Heading = styled('p')`
  font-size: 20px;
  text-align: center;

  padding: 20px 0 40px 0;
`;

const Download = styled('a')`
  color: #2080d9;
  text-decoration: underline;
`;

const Link = styled('a')`
  color: #2080d9;
  text-decoration: underline;
`;

const Detail = styled('p')`
  color: #2080d9;
  text-decoration: underline;
`;

const FetchingData = styled('div')`
  width: 100%;
  text-align: center;
  padding: 30px 0;
`;

const Registrant = ({ registrantData, loading }) => {
  const [data, setData] = useState([]);
  const [filterData, setFilterData] = useState({
    angkatan: '2023',
    keyword: '',
  });

  const defaultModalData = {
    id: 0,
    nim: '',
    name: '',
    years: '',
    email: '',
    portfolio: '',
    submitted: '',
    telegramID: '',
    linkedin: '',
    motivation: '',
    token: null,
  };

  const state = {
    user: zustandState((state) => state.user),
  };

  const [isModalOpen, setIsModalOpen] = useState(false);
  const [modalData, setModalData] = useState({
    ...defaultModalData,
    token: state.user && state.user.token,
  });

  const [tableTitle] = useState([
    {
      id: 'no',
      title: 'No',
      Cell: ({ row }) => {
        const { index } = row;

        return <p>{index + 1}</p>;
      },
    },
    {
      title: 'NIM',
      accessor: 'nim',
    },
    {
      title: 'Nama',
      accessor: 'nama_lengkap',
    },
    {
      title: 'Angkatan',
      accessor: 'angkatan',
    },
    {
      title: 'No HP',
      accessor: 'no_hp',
    },
    {
      title: 'Telegram',
      accessor: 'id_telegram',
    },
    {
      title: 'Link linkedin',
      accessor: 'linkedin',
      Cell: ({ value }) => {
        return (
          <Link target="_blank" rel="noopener noreferrer" href={`${value}`}>
            linkedin
          </Link>
        );
      },
    },
    // PORTOFOLIO
    // {
    //   id: 'portfolio',
    //   title: 'Portfolio',
    //   accessor: 'id',
    //   Cell: ({ value }) => {
    //     const { user } = state;
    //     const { baseURL } = process.env;

    //     return (
    //       <Download
    //         target="_blank"
    //         rel="noopener noreferrer"
    //         href={`${baseURL}/api/registrations/${value}/portfolio?token=${user.token}`}
    //       >
    //         Download
    //       </Download>
    //     );
    //   },
    // },
    {
      id: 'detail',
      accessor: 'id',
      title: 'Detail',
      Cell: ({ value }) => {
        return <Detail onClick={() => detailHandler(value)}>Lihat</Detail>;
      },
    },
    {
      id: 'waktu',
      title: 'Time',
      accessor: 'created_at',
      Cell: ({ value }) => {
        return <p>{moment(value).format('HH:mm')}</p>;
      },
    },
    {
      id: 'submitted',
      title: 'Submitted',
      accessor: 'created_at',
      Cell: ({ value }) => {
        return <p>{moment(value).format('DD/MM/YYYY')}</p>;
      },
    },
  ]);

  const detailHandler = (id) => {
    setModalData({ ...modalData, id });
  };

  const closeModal = () => {
    setIsModalOpen(false);
  };

  const controlsOnChangeHandler = (e) => {
    const { name, value } = e.target;

    setFilterData({
      ...filterData,
      [name]: value,
    });
  };

  useEffect(() => {
    const { angkatan, keyword } = filterData;

    if (keyword || angkatan) {
      let temp = [...registrantData];

      if (angkatan) {
        temp = temp.filter((item) => {
          const { created_at } = item;
          const date = moment(created_at).format('YYYY-MM');

          if (angkatan === '2022') {
            return date === `${angkatan}-02`;
          }

          return date >= `2022-11`;
        });
      }

      if (keyword) {
        temp = temp.filter(
          (item) =>
            item.nim.toString().includes(keyword) ||
            item.nama_lengkap.toLowerCase().includes(keyword.toLowerCase())
        );
      }

      setData([...temp]);
    } else {
      setData([...registrantData]);
    }
  }, [filterData]);

  useEffect(() => {
    const { id } = modalData;

    if (id) {
      const filtered = registrantData.filter((item) => item.id === id);

      if (filtered.length > 0) {
        const [item] = filtered;
        const {
          nim,
          email,
          angkatan,
          motivasi,
          portfolio,
          created_at,
          id_telegram,
          linkedin,
          nama_lengkap,
        } = item;

        setModalData({
          ...modalData,
          nim,
          email,
          portfolio,
          linkedin,
          years: angkatan,
          name: nama_lengkap,
          motivation: motivasi,
          submitted: created_at,
          telegramID: id_telegram,
        });

        setIsModalOpen(true);
      }
    } else {
      closeModal();
    }
  }, [modalData.id]);

  useEffect(() => {
    setData([...registrantData]);
  }, [registrantData]);

  useEffect(() => {
    const { angkatan } = filterData;

    let temp = [...registrantData];

    if (angkatan) {
      temp = temp.filter((item) => {
        const { created_at } = item;
        const date = moment(created_at).format('YYYY-MM');

        if (angkatan === '2022') {
          return date === `${angkatan}-02`;
        }

        return date >= `2022-11`;
      });
    }

    setData([...temp]);
  }, [registrantData]);

  return (
    <div>
      <Heading>List of Registrant</Heading>
      {registrantData.length > 0 && (
        <>
          <Controls data={data} filterData={filterData} onChange={controlsOnChangeHandler} />
          <ReactTable data={data} columns={tableTitle} />
        </>
      )}
      {registrantData.length === 0 && loading && (
        <FetchingData>
          <p>Fetching data...</p>
        </FetchingData>
      )}
      {registrantData.length === 0 && !loading && (
        <FetchingData>
          <p>No Data</p>
        </FetchingData>
      )}
      {isModalOpen && <Modal data={modalData} closeModal={closeModal} />}
    </div>
  );
};

export default Registrant;
