/* eslint-disable camelcase */
/* eslint-disable no-shadow */
/* eslint-disable react/prop-types */
/* eslint-disable no-use-before-define */

import moment from 'moment';
import styled from 'styled-components';
import React, { useState, useEffect } from 'react';

import { ReactTable } from 'components';

import Controls from './Controls';

const Heading = styled('p')`
  font-size: 20px;
  text-align: center;

  padding: 20px 0 40px 0;
`;

const FetchingData = styled('div')`
  width: 100%;
  text-align: center;
  padding: 30px 0;
`;

const EventRegistrant = ({ eventRegistrantData, loading }) => {
  const [data, setData] = useState([]);
  const [filterData, setFilterData] = useState({
    angkatan: '2023',
    keyword: '',
  });

  const [tableTitle] = useState([
    {
      id: 'no',
      title: 'No',
      Cell: ({ row }) => {
        const { index } = row;

        return <p>{index + 1}</p>;
      },
    },
    {
      title: 'NIM',
      accessor: 'nim',
    },
    {
      title: 'Nama',
      accessor: 'nama_lengkap',
    },
    {
      title: 'Email',
      accessor: 'email',
    },
    {
      title: 'Angkatan',
      accessor: 'angkatan',
    },
    {
      id: 'waktu',
      title: 'Time',
      accessor: 'created_at',
      Cell: ({ value }) => {
        return <p>{moment(value).format('HH:mm')}</p>;
      },
    },
    {
      id: 'submitted',
      title: 'Submitted',
      accessor: 'created_at',
      Cell: ({ value }) => {
        return <p>{moment(value).format('DD/MM/YYYY')}</p>;
      },
    },
  ]);

  const controlsOnChangeHandler = (e) => {
    const { name, value } = e.target;

    setFilterData({
      ...filterData,
      [name]: value,
    });
  };

  useEffect(() => {
    const { angkatan, keyword } = filterData;

    if (keyword || angkatan) {
      let temp = [...eventRegistrantData];

      if (angkatan) {
        temp = temp.filter((item) => {
          const { created_at } = item;
          const date = moment(created_at).format('YYYY-MM');

          if (angkatan === '2022') {
            return date === `${angkatan}-02`;
          }

          return date >= `2022-11`;
        });
      }

      if (keyword) {
        temp = temp.filter(
          (item) =>
            item.nim.toString().includes(keyword) ||
            item.nama_lengkap.toLowerCase().includes(keyword.toLowerCase())
        );
      }

      setData([...temp]);
    } else {
      setData([...eventRegistrantData]);
    }
  }, [filterData]);

  useEffect(() => {
    setData([...eventRegistrantData]);
  }, [eventRegistrantData]);
  return (
    <div>
      <Heading>List of Event Registrant</Heading>
      {eventRegistrantData.length > 0 && (
        <>
          <Controls data={data} filterData={filterData} onChange={controlsOnChangeHandler} />
          <ReactTable data={data} columns={tableTitle} />
        </>
      )}
      {eventRegistrantData.length === 0 && loading && (
        <FetchingData>
          <p>Fetching data...</p>
        </FetchingData>
      )}
      {eventRegistrantData.length === 0 && !loading && (
        <FetchingData>
          <p>No Data</p>
        </FetchingData>
      )}
    </div>
  );
};

export default EventRegistrant;
