/* eslint-disable react/prop-types */

import React from 'react';
import styled from 'styled-components';

import { Box, Button } from 'components';

const ModalBackground = styled('div')`
  width: 100%;
  height: 100%;

  position: fixed;
  left: 0;
  top: 0;
  z-index: 100;

  display: flex;

  background-color: rgba(0, 0, 0, 0.5);
`;

const ModalContent = styled(Box)`
  width: 100%;
  max-width: 50em;

  display: flex;
  flex-direction: column;

  background-color: #fff;

  border-radius: 16px;

  margin: 20px auto;
`;

const ModalTitle = styled('p')`
  font-size: 20px;
  font-weight: bold;

  text-align: center;
`;

const ModalInfo = styled('div')`
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  gap: 20px;
  overflow: auto;

  margin: 20px 0;
`;
const Image = styled('img')`
  width: 100%;

  border-radius: 0.5em;
  z-index: 100;
  cursor: pointer;
`;

const ModalButton = styled('div')`
  text-align: center;
`;

const Modal = ({ data, closeModal }) => {
  const { image } = data;

  return (
    <ModalBackground>
      <ModalContent p={27}>
        <ModalTitle>Bukti Pendaftaran Course</ModalTitle>
        <ModalInfo>
          <Image src={`${image}`} />
        </ModalInfo>
        <ModalButton>
          <Button onClick={closeModal}>Close</Button>
        </ModalButton>
      </ModalContent>
    </ModalBackground>
  );
};

export default Modal;
