import React, { useState, useEffect } from 'react';
import moment from 'moment';
import { zustandState } from 'services';
import { FormGroup, Label, Input, SubmitButton, FormContainer, FormTitleSelected } from './styles';
import Modal from './Modal';

const Setting = () => {
  const state = {
    user: zustandState((res) => res.user),
    setUser: zustandState((res) => res.setUser),
  };

  // const [isLoading, setIsLoading] = useState(false);
  const [formValues, setFormValues] = useState({
    pab_open_from: '',
    pab_open_until: '',
    assignment_open_from: '',
    assignment_open_until: '',
  });
  const [showSuccess, setShowSuccess] = useState(false);

  const { baseURL } = process.env;

  const formatPlannedDate = (date) => {
    const d = moment(date).format('YYYY-MM-DD HH:mm:ss');
    return d;
  };

  const handleChanges = (e) => {
    const { name, value } = e.target;
    setFormValues({
      ...formValues,
      [name]: formatPlannedDate(value),
    });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    // setIsLoading(true);

    const { user } = state;
    const myHeaders = new Headers();
    myHeaders.append('Accept', 'application/json');
    myHeaders.append('Content-Type', 'application/json');
    myHeaders.append('Authorization', `Bearer ${user.token}`);

    const raw = JSON.stringify(formValues);

    const requestOptions = {
      method: 'POST',
      headers: myHeaders,
      body: raw,
      redirect: 'follow',
    };

    try {
      await fetch(`${baseURL}/api/settings`, requestOptions)
        .then((response) => response.text())
        .then((result) => console.log(result))
        .catch((error) => console.log('error', error));
    } catch (error) {
      console.log(error);
    }

    setShowSuccess(true);
  };

  useEffect(() => {
    const response = async () => {
      await fetch(`${baseURL}/api/registration/status`, {
        method: 'GET',
        headers: {
          Accept: 'application/json',
        },
      })
        .then((res) => res.json())
        .then((result) => {
          setFormValues({
            ...formValues,
            pab_open_from: result.data.registration_open_from,
            pab_open_until: result.data.registration_open_until,
            assignment_open_from: result.data.assignment_open_from,
            assignment_open_until: result.data.assignment_open_until,
          });
        })
        .catch((error) => console.log('error', error));
    };
    response();
  }, []);

  return (
    <FormContainer>
      <FormTitleSelected>
        Setting Registration Form
        <hr />
      </FormTitleSelected>
      <form onSubmit={handleSubmit}>
        <FormGroup>
          <Label htmlFor="name">Registration Open From : </Label>
          <Input
            type="datetime-local"
            name="pab_open_from"
            step="1"
            onChange={handleChanges}
            defaultValue={formValues.pab_open_from}
            required
          />
          <Label htmlFor="name">Registration Open Until : </Label>
          <Input
            type="datetime-local"
            name="pab_open_until"
            step="1"
            onChange={handleChanges}
            defaultValue={formValues.pab_open_until}
            required
          />
          <Label htmlFor="name">Assignment Open From : </Label>
          <Input
            type="datetime-local"
            name="assignment_open_from"
            step="1"
            onChange={handleChanges}
            defaultValue={formValues.assignment_open_from}
            required
          />
          <Label htmlFor="name">Assignment Open Until : </Label>
          <Input
            type="datetime-local"
            name="assignment_open_until"
            step="1"
            onChange={handleChanges}
            defaultValue={formValues.assignment_open_until}
            required
          />
        </FormGroup>
        <SubmitButton type="submit" variant="primary">
          Setting Now
        </SubmitButton>
      </form>
      <Modal
        title="Your setting registration form has been submitted"
        onClose={() => setShowSuccess(false)}
        show={showSuccess}
      >
        {' '}
      </Modal>
    </FormContainer>
  );
};

export default Setting;
