import styled, { css } from 'styled-components';
import PropTypes from 'prop-types';

const Root = styled.div`
  position: absolute;
  left: ${(props) => (props.align === 'left' ? 0 : 'initial')};
  right: ${(props) => (props.align === 'right' ? 0 : 'initial')};

  ${(props) =>
    props.valign === 'middle' &&
    css`
      transform: translateY(50%);
    `}

  ${(props) =>
    props.valign === 'bottom' &&
    css`
      transform: translateY(100%);
    `}

  width: ${(props) => props.width};
  height: ${(props) => props.height};
  background-color: ${(props) => props.theme.colors.foreground};
  border-radius: ${(props) => (props.align === 'left' ? '0 16px 16px 0' : '16px 0 0 16px')};
  z-index: -1;
`;

const FloatingBackground = ({ align, valign, height, width }) => (
  <Root align={align} valign={valign} height={height} width={width} />
);

FloatingBackground.propTypes = {
  align: PropTypes.oneOf(['left', 'right']),
  valign: PropTypes.oneOf(['top', 'middle', 'bottom']),
  width: PropTypes.string,
  height: PropTypes.string,
};

FloatingBackground.defaultProps = {
  align: 'left',
  valign: 'top',
  width: '70vw',
  height: '579px',
};

export default FloatingBackground;
