/* eslint-disable camelcase */
/* eslint-disable no-shadow */
/* eslint-disable react/prop-types */

import moment from 'moment';
import styled from 'styled-components';
import React, { useState, useEffect } from 'react';

import { ReactTable } from 'components';

import Controls from './Controls';
import Modal from './Modal';

const Heading = styled('p')`
  font-size: 20px;
  text-align: center;

  padding: 20px 0 40px 0;
`;

const Download = styled('a')`
  color: #2080d9;
  text-decoration: underline;
`;

const FetchingData = styled('div')`
  width: 100%;
  text-align: center;
  padding: 30px 0;
`;
const Image = styled('img')`
  width: 100%;
  max-height: 20em;
  border-radius: .5em;
  z-index: 100;
  cursor: pointer;
  &:hover {
    transform: scale(1.2);
    transition: transform 0.5s;

  `;

const Assignment = ({ assignmentData, loading }) => {
  const [data, setData] = useState([...assignmentData]);
  const [modalData, setModalData] = useState({});
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [filterData, setFilterData] = useState({
    materi: '',
    keyword: '',
    angkatan: '2023',
  });
  const [tableTitle] = useState([
    {
      id: 'no',
      title: 'No',
      Cell: ({ row }) => {
        const { index } = row;

        return <p>{index + 1}</p>;
      },
    },
    {
      title: 'NIM',
      accessor: 'nim',
    },
    {
      title: 'Nama',
      accessor: 'nama_lengkap',
    },
    // {
    //   title: 'Materi',
    //   accessor: 'materi',
    //   Cell: ({ value }) => {
    //     return value.toUpperCase();
    //   },
    // },
    {
      title: 'Link',
      accessor: 'link_tugas',
      Cell: ({ value }) => {
        return (
          <Download target="_blank" rel="noopener noreferrer" href={value}>
            Open in a new tab
          </Download>
        );
      },
    },
    {
      title: 'Course',
      accessor: 'course',
      Cell: ({ value }) => {
        const course = JSON.parse(value);
        return (
          <Download target="_blank" rel="noopener noreferrer" href={course.url}>
            {course.name}
          </Download>
        );
      },
    },
    {
      id: 'waktu',
      title: 'Time',
      accessor: 'created_at',
      Cell: ({ value }) => {
        return <p>{moment(value).format('HH:mm')}</p>;
      },
    },
    {
      id: 'submitted',
      title: 'Submitted',
      accessor: 'created_at',
      Cell: ({ value }) => {
        return <p>{moment(value).format('DD/MM/YYYY')}</p>;
      },
    },
  ]);
  const closeModal = () => {
    setIsModalOpen(false);
  };

  const controlsOnChangeHandler = (e) => {
    const { name, value } = e.target;

    setFilterData({
      ...filterData,
      [name]: value,
    });
  };

  useEffect(() => {
    const { materi, keyword, angkatan } = filterData;

    if (materi || keyword || angkatan) {
      let temp = [...assignmentData];

      if (angkatan) {
        temp = temp.filter((item) => {
          const { created_at } = item;
          const date = moment(created_at).format('YYYY-MM');

          if (angkatan === '2022') {
            return date === `2022-02` || date === `2022-03`;
          }

          return date >= `2022-11`;
        });
      }

      if (materi) {
        temp = temp.filter((item) => item.materi === materi);
      }

      if (keyword) {
        temp = temp.filter(
          (item) =>
            item.nim.toString().includes(keyword) ||
            item.nama_lengkap.toLowerCase().includes(keyword.toLowerCase())
        );
      }

      setData([...temp]);
    } else {
      setData([...assignmentData]);
    }
  }, [filterData]);

  useEffect(() => {
    setData([...assignmentData]);
  }, [assignmentData]);

  useEffect(() => {
    const { angkatan } = filterData;

    let temp = [...assignmentData];

    if (angkatan) {
      temp = temp.filter((item) => {
        const { created_at } = item;
        const date = moment(created_at).format('YYYY-MM');

        if (angkatan === '2022') {
          return date === `${angkatan}-02`;
        }

        return date >= `2022-11`;
      });
    }

    setData([...temp]);
  }, [assignmentData]);

  return (
    <div>
      <Heading>List of Assignment</Heading>
      {assignmentData.length > 0 && (
        <>
          <Controls data={data} filterData={filterData} onChange={controlsOnChangeHandler} />
          <ReactTable data={data} columns={tableTitle} />
        </>
      )}
      {assignmentData.length === 0 && loading && (
        <FetchingData>
          <p>Fetching data...</p>
        </FetchingData>
      )}
      {assignmentData.length === 0 && !loading && (
        <FetchingData>
          <p>No Data</p>
        </FetchingData>
      )}
      {isModalOpen && <Modal data={modalData} closeModal={closeModal} />}
    </div>
  );
};

export default Assignment;
