/* eslint-disable no-shadow */

import { remove } from 'js-cookie';
import fetch from 'isomorphic-unfetch';
import styled from 'styled-components';
import React, { useState, useEffect } from 'react';
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';

import { withUserAuth } from 'hoc';
import { Button } from 'components';
import { zustandState } from 'services';

import { Assignment, EventRegistrant, Registrant, Setting } from './section';

const ContentContainer = styled('div')`
  width: 100%;

  .react-tabs__tab--selected,
  .react-tabs__tab-list {
    border: none;
  }

  .react-tabs__tab {
    outline: none;
    font-weight: bold;

    hr {
      color: white;
      margin-top: 8px;
      width: 50%;
      border: 1px solid;
    }
  }

  .react-tabs__tab--selected {
    color: #2080d9;

    hr {
      color: #2080d9;
    }
  }
`;

const ButtonContainer = styled('div')`
  width: 100%;
  display: flex;
  justify-content: center;

  margin-top: 60px;
`;

const Content = () => {
  const [index, setIndex] = useState(0);
  const [isLoggingOut, setIsLoggingOut] = useState(false);
  const [loading, setLoading] = useState(true);
  const [registrantData, setRegistrantData] = useState([]);
  const [eventRegistrantData, setEventRegistrantData] = useState([]);
  const [assignmentData, setAssignmentData] = useState([]);

  const state = {
    user: zustandState((state) => state.user),
    setUser: zustandState((state) => state.setUser),
  };

  const tabsOnSelectHandler = (currentIndex) => {
    setIndex(currentIndex);
  };

  const getRegistrantData = async () => {
    const { user } = state;
    const { baseURL } = process.env;

    try {
      const registrantResponse = await fetch(
        `${baseURL}/api/registrations?orderby=created_at&sort=desc`,
        {
          method: 'GET',
          headers: {
            Accept: 'application/json',
            Authorization: `Bearer ${user.token}`,
          },
        }
      );

      const eventRegistrantResponse = await fetch(
        `${baseURL}/api/registration/event?orderby=created_at&sort=desc`,
        {
          method: 'GET',
          headers: {
            Accept: 'application/json',
            Authorization: `Bearer ${user.token}`,
          },
        }
      );

      const assignmentResponse = await fetch(
        `${baseURL}/api/assignments?orderby=created_at&sort=desc`,
        {
          method: 'GET',
          headers: {
            Accept: 'application/json',
            Authorization: `Bearer ${user.token}`,
          },
        }
      );

      const registrantResponseToJSON = await registrantResponse.json();
      const eventRegistrantResponseToJSON = await eventRegistrantResponse.json();
      const assignmentResponseToJSON = await assignmentResponse.json();
      console.log(eventRegistrantResponseToJSON);
      assignmentResponseToJSON.data.sort((a, b) => {
        const dateA = new Date(a.created_at);
        const dateB = new Date(b.created_at);

        return dateB - dateA;
      });

      if (
        registrantResponseToJSON.status === 200 ||
        assignmentResponseToJSON.status === 200 ||
        eventRegistrantResponseToJSON.status === 200
      ) {
        setRegistrantData(registrantResponseToJSON.data);
        setEventRegistrantData(eventRegistrantResponseToJSON.data);
        setAssignmentData(assignmentResponseToJSON.data);
      }
    } catch (error) {
      //
    } finally {
      setLoading(false);
    }
  };

  const logoutHandler = async () => {
    const { baseURL } = process.env;
    const { user, setUser } = state;
    const { tokenName } = process.env;

    setIsLoggingOut(true);

    await fetch(`${baseURL}/api/logout`, {
      method: 'GET',
      headers: {
        Accept: 'application/json',
        Authorization: `Bearer ${user.token}`,
      },
    });

    remove(tokenName);
    setUser(null);
  };

  useEffect(() => {
    getRegistrantData();
  }, []);

  return (
    <ContentContainer>
      <Tabs selectedIndex={index} onSelect={tabsOnSelectHandler}>
        <TabList>
          <Tab>
            Registrant
            <hr />
          </Tab>
          <Tab>
            Event Registrant
            <hr />
          </Tab>
          <Tab>
            Assignment
            <hr />
          </Tab>
          <Tab>
            Setting
            <hr />
          </Tab>
        </TabList>

        <TabPanel>
          <Registrant registrantData={registrantData} loading={loading} />
        </TabPanel>
        <TabPanel>
          <EventRegistrant eventRegistrantData={eventRegistrantData} loading={loading} />
        </TabPanel>
        <TabPanel>
          <Assignment assignmentData={assignmentData} loading={loading} />
        </TabPanel>
        <TabPanel>
          <Setting />
        </TabPanel>
      </Tabs>

      <ButtonContainer>
        <Button onClick={logoutHandler} disabled={isLoggingOut}>
          Log Out
        </Button>
      </ButtonContainer>
    </ContentContainer>
  );
};

export default withUserAuth(Content);
